/**
 * todo TOOLS
 */
import {WinJSDec, appError, fnError} from './APP/config';

var name = 'ACTION_SHEET';
var ACTION_SHEET = (title, buttons, fn = _ => {
}) => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      window.WinJSBridge.call('tools', 'actionSheet', {title, buttons}, res => {
        var {code, response} = res;
        if (code == 0) {
          fn(response);
        } else {
          throw new Error(`[${name}] ${appError}`);
        }
      });
    });
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default ACTION_SHEET;
